@import "../../global.scss";

.playerContainer {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
  max-width: 100%;
}
.playerContainer iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.noVideoSelected {
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: 32px;
}
